import react, {Component} from 'react';
import {Navbar, NavbarBrand, NavbarItem} from 'bloomer';


const myNav={
    border: '',
    backgroundColor: '#303F9F',
}



export default class navbar extends Component{
  
    render(){
        return(<Navbar style={myNav}>
            <NavbarBrand>
                <NavbarItem>
                    Football coin
                </NavbarItem>
            </NavbarBrand>
        </Navbar>)
    }
}


